@font-face {font-family: "DS-Digital";
	src: url(/rect-snek/static/media/DS-Digital.37c8019a.eot);
  src:
    url(/rect-snek/static/media/DS-Digital-iefix.37c8019a.eot) format("embedded-opentype"),
    url(/rect-snek/static/media/DS-Digital.ce409a4d.woff2) format("woff2"),
		url(/rect-snek/static/media/DS-Digital.223f2246.ttf) format("truetype"),
		url(/rect-snek/static/media/DS-Digital.cad634a7.svg) format("svg");
}
body {
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
}
.root {
  width: 100%;
  height: 100%;
  margin:0;
  line-height: 0;
  background-color: #D9DBC5;
  padding-top: 5px;
}
@media all and (min-height: 400px) {
  .root {
    padding-top: 1.25vh;
  }
}
.snek {
  position: relative;
  margin: 0 auto;
  width: 57vh;
  min-width: 200px;
  height: 100%;
}
.display {
  width: 100%;
  height: 70vh;
}
.display__edging {
  width: 47vh;
  min-width: 188px;
  margin: 0 auto;
  padding: 0 3.5px 3.5px;
  border: 2px solid #911;
}
@media all and (min-height: 400px) {
  .display__edging {
    padding: 0 1vh 1vh;
    border-width: .5vh;
    font-size: 4vh;
  }
}
.display__legend {
margin: 0 auto;
font-family: impact;
height: 4vh;
width: 24vh;
min-height: 16px;
min-width: 96px;
background-size: 100% 100%;
background-image: url(/rect-snek/static/media/legend.9a918d49.svg);
border-left: 5px solid #D9DBC5;
border-right: 5px solid #D9DBC5;
}
@media all and (min-height: 400px) {
  .display__legend {
    border-left-width: 1.25vh;
    border-right-width: 1.25vh;
  }
}
.display__borders {
  margin: 0 auto;
  width: 42vh;
  height: 58vh;
  min-width: 168px;
  min-height: 234px;
  background-color: #909D83;
  border-width: 10px;
  border-style: solid;
  border-radius: 2px;
  border-top-color: #d6d8c3;
  border-left-color: #bdbfae;
  border-bottom-color: #caccb7;
  border-right-color: #e3e5ce;
}
@media all and (min-height: 400px) {
  .display__borders {
    border-width: 2.5vh;
    border-radius: .5vh;
  }
}

.display__glass {
  width: 100%;
  height: 100%;
  border-radius: 2px;
  box-shadow:inset 1px 1px 4px 0 #313729, 4px 0 4px 0 #f8f9e3, -4px 0 4px 0 #97998c;
}
@media all and (min-height: 400px) {
  .display__glass {
    border-radius: .5vh;
    box-shadow:inset .25vh .25vh 1vh 0 #313729, 1vh 0 1vh 0 #f8f9e3, -1vh 0 1vh 0 #97998c;
  }
}
.display__frame {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  float: left;
  width: 27.5vh;
  height: 55vh;
  min-width: 110px;
  min-height: 220px;
  outline: 2px solid #313729;
  margin: 4% 0 4% 3%;
  padding: 0 2px;
}
@media all and (min-height: 400px) {
  .display__frame {
    outline: .5vh solid #313729;
    padding: 0 .5vh;
  }
}
.display__side {
  position: relative;
  float: right;
  width: 10vh;
  height: 55vh;
  min-width: 40px;
  min-height: 222px;
  margin: 2% 3% 2% 0;
  color: #313729;
}
.display__score {
  text-align: center;
  font-family: arial;
  font-style: italic;
  font-size: 7px;
  line-height: 15px;
  margin-bottom: 5px;
}
@media all and (min-height: 400px) {
  .display__score {
    font-size: 1.75vh;
    line-height: 3.75vh;
    margin-bottom: 1.25vh;
  }
}
.display__score-value {
  margin: 0;
  font-style: normal;
  line-height: 15px;
  font-family: "DS-Digital";
  font-size: 14px;
}
@media all and (min-height: 400px) {
  .display__score-value {
    font-size: 3.5vh;
    line-height: 3.75vh;
  }
}
.display__hi-score {
  text-align: center;
  font-family: arial;
  font-style: italic;
  font-size: 7px;
  line-height: 15px;
  margin-bottom: 5px;
}
@media all and (min-height: 400px) {
  .display__hi-score {
    font-size: 1.75vh;
    line-height: 3.75vh;
    margin-bottom: 1.25vh;
  }
}
.display__hi-score-value {
  margin: 0;
  font-style: normal;
  line-height: 15px;
  font-family: "DS-Digital";
  font-size: 14px;
}
@media all and (min-height: 400px) {
  .display__hi-score-value {
    font-size: 3.5vh;
    line-height: 3.75vh;
  }
}
.display__level {
  text-align: center;
  font-family: arial;
  font-style: italic;
  font-size: 7px;
  line-height: 15px;
  margin-top: 12px;
  margin-bottom: 5px;
}
@media all and (min-height: 400px) {
  .display__level {
    font-size: 1.75vh;
    line-height: 3.75vh;
    margin-top: 3vh;
    margin-bottom: 1.25vh;
  }
}
.display__level-value {
  margin: 0;
  font-style: normal;
  line-height: 15px;
  font-family: "DS-Digital";
  font-size: 25px;
  margin-top: 4px;
}
@media all and (min-height: 400px) {
  .display__level-value {
    font-size: 6.25vh;
    margin-top: 1vh;
  }
}
.display__speed {
  text-align: center;
  font-family: arial;
  font-style: italic;
  font-size: 7px;
  line-height: 15px;
  margin-bottom: 5px;
}
@media all and (min-height: 400px) {
  .display__speed {
    font-size: 1.75vh;
    line-height: 3.75vh;
    margin-bottom: 1.25vh;
  }
}
.display__speed-value {
  margin: 0;
  font-style: normal;
  line-height: 15px;
  font-family: "DS-Digital";
  font-size: 25px;
  margin-top: 15px;
  margin-bottom: 4px;
}
@media all and (min-height: 400px) {
  .display__speed-value {
    font-size: 6.25vh;
    margin-top: 3.75vh;
    margin-bottom: 1vh;
  }
}
.display__pause {
  position: relative;
  text-align: center;
  font-family: arial;
  font-style: italic;
  font-size: 7px;
  line-height: 15px;
  margin-top: 20px;
  margin-bottom: 5px;
  color: #7E916C;
  fill: #7E916C;
}
@media all and (min-height: 400px) {
  .display__pause {
    margin-top: 5vh;
  }
}
.display__pause-svg {
  height: 5vh;
  width: 5vh;
  min-height: 25px;
  min-width: 25px;
  margin-top: 4px;
}
@media all and (min-height: 400px) {
  .display__pause-svg {
    margin-top: 1vh;
  }
}
.controls {
  width: 100%;
  height: 28.75vh;
}
